<template>
  <div class="container">
    <div class="row mx-0 mb-5 justify-content-center">
      <form
        class="post_form col-lg-8 col-md-12 col-sm-12 col-12 pt-4"
        @submit.prevent="submitHandler"
      >
        <div class="post_form-title">
          <span>Новый участок</span>
        </div>
        <div class="form-group">
          <label for="postTitle" class="required">Название</label>
          <div class="formWithSupText">
            <input
              id="postTitle"
              v-model.trim="title"
              type="text"
              class="post_form-control"
              autocomplete="off"
            />
            <span
              :class="[
                $v.title.$dirty && !$v.title.required ? 'invalid' : 'd-none',
              ]"
              >Заполните это поле</span
            >
          </div>
        </div>
        <div class="form-group">
          <label for="postLocation" class="required">Расположение</label>
          <div class="formWithSupText">
            <input
              id="postLocation"
              v-model.trim="location"
              type="text"
              class="post_form-control"
              placeholder="Адрес"
              autocomplete="off"
            />
            <span
              :class="[
                $v.location.$dirty && !$v.location.required
                  ? 'invalid'
                  : 'd-none',
              ]"
            >
              Заполните это поле
            </span>
          </div>
        </div>
        <div class="form-group">
          <label for="postSpace" class="required">Площадь</label>
          <div class="formWithSupText">
            <input
              id="postSpace"
              v-model.trim="space"
              type="number"
              min="0"
              step="any"
              class="post_form-control w-25"
              autocomplete="off"
            />
            <span class="post_form-suptext">га</span>
            <span
              :class="[
                $v.space.$dirty && !$v.space.required ? 'invalid' : 'd-none',
              ]"
            >
              Заполните это поле
            </span>
          </div>
        </div>
        <div class="form-group">
          <label>Целевое назначение</label>
          <div class="form-check">
            <input
              v-model="categories"
              class="form-check-input"
              type="checkbox"
              value="Товарное сельское хозяйство"
            />
            <span>Товарное сельское хозяйство</span>
          </div>
          <div class="form-check">
            <input
              v-model="categories"
              class="form-check-input"
              type="checkbox"
              value="Коммерческое"
            />
            <span>Коммерческое</span>
          </div>
          <div class="form-check">
            <input
              v-model="categories"
              class="form-check-input"
              type="checkbox"
              value="Многоэтажное жилищное строительство"
            />
            <span>Многоэтажное жилищное строительство</span>
          </div>
          <div class="form-check">
            <input
              v-model="categories"
              class="form-check-input"
              type="checkbox"
              value="Индивидуальное жилищное строительство"
            />
            <span>Индивидуальное жилищное строительство</span>
          </div>
        </div>
        <div class="form-group">
          <label for="image-upload" class="required">Изображения</label>
          <div class="formWithSupText">
            <input
              id="image-upload"
              v-model.trim="inputImage"
              type="text"
              class="post_form-control"
              placeholder="Вставьте ссылку и нажмите Enter"
              autocomplete="off"
              @keydown.enter.prevent="addToImages(inputImage)"
            />
            <span
              v-if="$v.inputImage.$dirty && !$v.inputImage.url"
              :class="[
                $v.inputImage.$dirty && !$v.inputImage.url
                  ? 'invalid'
                  : 'd-none',
              ]"
              >Укажите ссылку</span
             >
            <span
              v-else-if="$v.images.$dirty && !$v.images.required"
              :class="[
                $v.images.$dirty && !$v.images.required ? 'invalid' : 'd-none',
              ]"
             >
              Минимальное кол-во изображений 1
            </span>
            <span
              v-else-if="$v.images.$dirty && !$v.images.maxLength"
              :class="[
                $v.images.$dirty && !$v.images.maxLength ? 'invalid' : 'd-none',
              ]"
             >
              Максимальное кол-во изображений 10
            </span>
          </div>
          <div v-if="images.length" class="imagesPreviewWrapper">
            <div
              v-for="(image, idx) of images"
              :key="idx"
              class="imagePreviewContainer"
            >
              <img :src="image" class="image-preview" />
              <button
                type="button"
                class="close"
                aria-label="Close"
                @click="removeFromImages(idx)"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
          </div>
        </div>
        <div class="form-group">
          <label for="postDescription">Описание поста</label>
          <textarea
            id="postDescription"
            v-model.trim="description"
            class="post_form-control"
            rows="5"
          ></textarea>
        </div>
        <div class="form-group">
          <div class="form-check">
            <input
              v-model="hot"
              class="form-check-input"
              type="checkbox"
              value="Активные торги"
            />
            <span>Активные торги</span>
          </div>
        </div>
        <div class="form-group">
          <div class="formWithSupText">
            <Datepicker
              v-model="date"
              format="dd.MM.yyyy"
              input-class="post_form-control w-25"
              :language="ru"
              :clear-button="true"
              placeholder="Дата торгов"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="postPrice">Оценочная стоимость</label>
          <div class="formWithSupText">
            <input
              id="postPrice"
              v-model.number.trim="price"
              type="number"
              class="post_form-control w-25"
              autocomplete="off"
            />
            <span class="post_form-suptext">тенге</span>
          </div>
        </div>
        <button type="submit" class="btn btn-create">Добавить</button>
      </form>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import { ru } from 'vuejs-datepicker/dist/locale'
import { required, url, maxLength } from 'vuelidate/lib/validators'

export default {
  name: 'CreatePost',
  components: { Datepicker },
  data: () => ({
    ru,
    title: '',
    description: '',
    location: '',
    space: '',
    categories: [],
    price: '',
    date: null,
    hot: false,
    inputImage: '',
    images: [],
  }),
  validations: {
    title: {
      required,
    },
    space: {
      required,
    },
    location: {
      required,
    },
    images: {
      required,
      maxLength: maxLength(10),
    },
    inputImage: {
      url,
    },
  },
  methods: {
    async submitHandler() {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      const postData = {
        title: this.title || '',
        description: this.description || '',
        price: this.price || '',
        location: this.location || '',
        space: this.space || '',
        date: this.date ? new Date(this.date).toLocaleDateString() : null,
        categories: this.categories || [],
        hot: this.hot || false,
        images: this.images || [],
      }
      try {
        const post = await this.$store.dispatch('createPost', postData)
        console.log(post)
        this.$router.push(`/catalog`)
        this.$toast.open({
          message: 'Пост был успешно создан!',
          type: 'success',
        })
        // eslint-disable-next-line no-empty
      } catch (error) {}
    },
    addToImages(image) {
      if (this.$v.inputImage.$invalid) {
        this.$v.inputImage.$touch()
        return
      }
      if (image) {
        this.images.push(image)
        this.inputImage = ''
      }
    },
    removeFromImages(index) {
      this.images.splice(index, 1)
    },
  },
}
</script>
